* {
  box-sizing: border-box;
}

.box-form {
  width: 100%;
  height: 200%;
  background-color: #228b22;
  padding: 15px;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.search-form {
  margin-left: 10%;
}

input {
  width: 80%;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .15));
  border-style: none;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 30px;
  outline: rgba(0, 0, 0, 0);
  padding: 10px 15px;
  font-size: 16px;
}

button {
  cursor: pointer;
  background-color: #fff;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 30px;
  padding: 10px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

button:hover {
  background-color: #71f671;
  transform: scale(1.05);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 2px 1px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
