  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 25px;
  padding-top: 25px;
  display: flex;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  border-color: none;
  width: 50%;
  color: #fff;
  cursor: pointer;
  background-color: #228b22;
  margin: 20px auto;
